// @ts-nocheck
import React, { useState } from "react";
import "./style.css";
import { useForm } from "react-hook-form";
import { useNavigate } from "react-router-dom";
import "../Login/login.css";

export const Register = () => {
  const [registerData, setRegisterData] = useState({
    name: "",
    userName: "",
    email: "",
    password: "",
    regGender: false,
    age: "",
  });
  const navigate = useNavigate();

  const { name, userName, email, password, regGender, age } = registerData;

  const registerUser = async (userData) => {
    if (userData.regGender === "man") {
      userData.regGender = true;
    } else if (userData.regGender === "wooman") {
      userData.regGender = false;
    }

    const options = {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        name: userData.name,
        username: userData.userName,
        email: userData.email,
        password: userData.password,
        age: userData.age,
        isMan: userData.regGender,
      }),
    };

    const urlForReg =
      "https://first-node-js-app-r.herokuapp.com/api/users/register";
    const response = await fetch(urlForReg, options);
    const responseData = await response.json();
    console.log(responseData);
  };

  const {
    register,
    formState: { errors },
    handleSubmit,
    reset,
  } = useForm({ mode: "onChange" });

  const onSubmit = (data) => {
    setRegisterData({
      name: data.name,
      userName: data.userName,
      email: data.email,
      password: data.password,
      regGender: data.regGender,
      age: data.age,
    });
    registerUser(data);
    reset();
  };
  const [checked, isChecked] = useState(false);

  return (
    <form className="wrapper" onSubmit={handleSubmit(onSubmit)}>
      <h3> </h3>
      <label>
          
        <input
          type="text"
          {...register("name", {
            required: true,
            minLength: {
              value: 2,
              message: "  ,   2 ",
            },
            maxLength: {
              value: 20,
              message: "  ,   20 ",
            },
          })}
          placeholder="  "
        />
        {errors.name && <p>{errors.name.message}</p>}
      </label>

      <label>
          
        <input
          type="text"
          {...register("userName", {
            required: true,
            minLength: {
              value: 2,
              message: " ,   2 ",
            },
            maxLength: {
              value: 20,
              message: " ,   20 ",
            },
          })}
          placeholder="  "
        />
        {errors.userName && <p>{errors.userName.message}</p>}
      </label>
      <label>
          
        <input
          type="email"
          {...register("email", {
            required: true,
            minLength: {
              value: 3,
              message: "  ,   3 ",
            },
          })}
          placeholder="  "
        />
      </label>
      {errors.email && <p>{errors.email.message}</p>}

      <label>
          
        <input
          type="password"
          {...register("password", {
            required: "   ",
            minLength: {
              value: 8,
              message: "  ,   8 ",
            },
            maxLength: {
              value: 20,
              message:
                "  ,   20 ",
            },
            pattern: {
              value:
                /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]+$/,
              message:
                "     1  , 1  , 1   1  ",
            },
          })}
          placeholder="  "
        />
      </label>
      {errors.password && <p>{errors.password.message}</p>}
      <label>
          
        <select
          {...register("regGender", {
            required: true,
          })}
        >
          <option> </option>
          <option value="man"></option>
          <option value="wooman"></option>
        </select>
      </label>
      <label>
          
        <input
          type="number"
          {...register("age", {
            required: true,
            minLength: {
              value: 1,
              message: "  1   ",
            },
            maxLength: {
              value: 2,
              message: "  2 ",
            },
          })}
          placeholder="  "
        />
        {errors.age && <p>{errors.age.message}</p>}
      </label>
      <label>
          
        <input
          type="checkbox"
          className="input-checkbox"
          checked={checked}
          onChange={() => {
            isChecked(!checked);
          }}
        />
      </label>
      <div className="button-block">
        <button type="submit" disabled={!checked}>
          
        </button>
        <button
          type="submit"
          onClick={() => {
            navigate("/");
          }}
        >
          
        </button>
      </div>
      {name && userName && email && password && regGender ? (
        <div>
          <h3>   </h3>
          <ul>
            <li> : {name}</li>
            <li> : {userName}</li>
            <li> : {email}</li>
            <li> : {password}</li>
            <li> : {regGender}</li>
            <li> : {age} </li>
          </ul>
        </div>
      ) : (
        <></>
      )}
    </form>
  );
};
